import { Injectable } from '@angular/core';

import { ConfigurationService } from './configuration.service';

@Injectable({
  providedIn: 'root',
})
export class AnalyticsLoaderService {
  private isGtmLoaded = false;

  constructor(private configurationService: ConfigurationService) {}

  loadAll() {
    this.loadGtm();
  }

  private loadGtm() {
    const configuration = this.configurationService.configuration;
    if (!configuration.gtmId || this.isGtmLoaded) {
      return;
    }

    // tslint:disable
    (function (w, d, s, l, i) {
      w[l] = w[l] || [];
      w[l].push({ 'gtm.start': new Date().getTime(), event: 'gtm.js' });
      var f = d.getElementsByTagName(s)[0],
        j = d.createElement(s) as HTMLScriptElement,
        dl = l != 'dataLayer' ? '&l=' + l : '';
      j.async = true;
      j.src = 'https://www.googletagmanager.com/gtm.js?id=' + i + dl;
      f.parentNode.insertBefore(j, f);
    })(window, document, 'script', 'dataLayer', `${configuration.gtmId}`);
    // tslint:enable

    this.isGtmLoaded = true;
  }
}
