import { Component, OnDestroy } from '@angular/core';
import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer } from '@angular/platform-browser';
import { ErrorResponseAction, UserRequestConnectionAction } from '@boa-ga/api-interfaces';
import { TranslateService } from '@ngx-translate/core';
import { Angulartics2GoogleTagManager } from 'angulartics2/gtm';
import { Subject } from 'rxjs';
import { distinctUntilChanged, map, take, takeUntil } from 'rxjs/operators';

import { AnalyticsLoaderService } from './services/analytics-loader.service';
import { AuthService } from './services/auth.service';
import { ConfigurationService } from './services/configuration.service';
import { NotificationService } from './services/notification.service';
import { SocketService } from './services/socket.service';
import { UserSettingsService } from './services/user-settings.service';

@Component({
  selector: 'bg-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnDestroy {
  private tearDown$ = new Subject();

  constructor(
    matIconRegistry: MatIconRegistry,
    domSanitizer: DomSanitizer,
    translate: TranslateService,
    angulartics: Angulartics2GoogleTagManager,
    userSettingsService: UserSettingsService,
    socketService: SocketService,
    authService: AuthService,
    notificationService: NotificationService,
    configurationService: ConfigurationService,
    analyticsLoaderService: AnalyticsLoaderService,
  ) {
    // analytics
    analyticsLoaderService.loadAll();
    angulartics.startTracking();

    matIconRegistry.addSvgIconSet(
      domSanitizer.bypassSecurityTrustResourceUrl('./assets/svg/sprite.svg'),
    );

    socketService.init();
    socketService.error$
      .pipe(takeUntil(this.tearDown$))
      .subscribe((error) => notificationService.showMessage(error));

    authService.init().pipe(takeUntil(this.tearDown$)).subscribe();
    userSettingsService.init();

    translate.setDefaultLang(
      configurationService.configuration.supportedLanguages[0],
    );
    userSettingsService.userSettings$
      .pipe(take(1), takeUntil(this.tearDown$))
      .subscribe((userSettings) => {
        translate.addLangs(userSettings.availableLanguages);

        socketService.emit(
          new UserRequestConnectionAction({
            user: {
              id: userSettings.id,
              name: userSettings.name,
              language: userSettings.language,
            },
            password: userSettings.password,
          }),
        );
      });
    userSettingsService.userSettings$
      .pipe(
        map((s) => s.language),
        distinctUntilChanged(),
        takeUntil(this.tearDown$),
      )
      .subscribe((selectedLanguage) => translate.use(selectedLanguage));

    socketService
      .listen(ErrorResponseAction)
      .pipe(takeUntil(this.tearDown$))
      .subscribe((action) => notificationService.showMessage(action.payload));

    socketService
      .listenAll()
      .pipe(takeUntil(this.tearDown$))
      .subscribe((action) =>
        console.log(`${action.type} ${JSON.stringify(action.payload)}`),
      );
  }

  ngOnDestroy() {
    this.tearDown$.next();
    this.tearDown$.complete();
  }
}
