import { HttpErrorResponse, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ErrorResponseDto } from '@boa-ga/api-interfaces';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { NotificationService } from './notification.service';

@Injectable()
export class ErrorInterceptor implements HttpInterceptor {
  constructor(
    private notificationService: NotificationService,
    private loggerService: LoggerService,
  ) {}

  intercept(
    request: HttpRequest<any>,
    next: HttpHandler,
  ): Observable<HttpEvent<any>> {
    return next.handle(request).pipe(
      catchError((response: HttpErrorResponse) => {
        const serverError = response.error as ErrorResponseDto;
        const errorMessage = serverError.errorMessage || response.message;

        this.loggerService.error(errorMessage);
        this.notificationService.showMessage(errorMessage);

        return throwError(response);
      }),
    );
  }
}
