import { Component, OnDestroy, OnInit } from '@angular/core';
import { Sort } from '@angular/material/sort';
import { Router } from '@angular/router';
import {
  GameCreatedAction,
  GameDto,
  GameRemovedAction,
  GameType,
  GameUserJoinedAction,
  GameUserLeftAction,
} from '@boa-ga/api-interfaces';
import { Subject } from 'rxjs';
import { debounceTime, startWith, switchMap, takeUntil, tap } from 'rxjs/operators';

import { SocketService } from '../../services/socket.service';
import { GameTypeNames } from '../game-type-names';
import { GamesService } from '../games.service';

@Component({
  selector: 'bg-games-list',
  templateUrl: './games-list.component.html',
  styleUrls: ['./games-list.component.scss'],
})
export class GamesListComponent implements OnInit, OnDestroy {
  gameTypeName = GameTypeNames;
  isLoading = true;
  games: GameDto[] = [];
  gameTypeIcons: { [index: number]: string } = {
    [GameType.Munchkin]: 'icon-enter',
    [GameType.Test]: 'icon-bug',
  };

  private tearDown$ = new Subject();

  constructor(
    private gamesService: GamesService,
    private socketService: SocketService,
    private router: Router,
  ) {}

  ngOnInit() {
    this.socketService
      .listenAnyOf(
        GameCreatedAction,
        GameRemovedAction,
        GameUserJoinedAction,
        GameUserLeftAction,
      )
      .pipe(
        startWith(-1),
        debounceTime(1000),
        switchMap(() => this.gamesService.getAllRunningGames()),
        tap(() => (this.isLoading = true)),
        takeUntil(this.tearDown$),
      )
      .subscribe(
        (games) => {
          this.isLoading = false;
          this.games = games;
        },
        () => {
          this.isLoading = false;
        },
      );
  }

  joinGame(game: GameDto) {
    this.router.navigate(['games', GameType[game.type].toLowerCase(), game.id]);
  }

  ngOnDestroy() {
    this.tearDown$.next();
    this.tearDown$.complete();
  }

  sortData(sort: Sort) {
    const data = this.games.slice();
    if (!sort.active || sort.direction === '') {
      this.games = data;
      return;
    }

    this.games = data.sort((a, b) => {
      const isAsc = sort.direction === 'asc';
      switch (sort.active) {
        case 'type':
          return compare(a.type, b.type, isAsc);
        case 'name':
          return compare(a.name, b.name, isAsc);
        case 'players':
          return compare(a.players.length, b.players.length, isAsc);
        case 'created':
          return compare(
            a.createdDate.getDate(),
            b.createdDate.getDate(),
            isAsc,
          );
        default:
          return 0;
      }
    });
  }
}

function compare(a: number | string, b: number | string, isAsc: boolean) {
  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}
